import Vue from 'vue'
import Router from 'vue-router'
import Header from "@/components/Header.vue";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        header: Header,
        // main: Home,
        main: () => import('@/views/Home.vue'),
        footer: () => import('@/components/Footer.vue')
      }
    },
    {
      path: '/m/device/:pk/:dn',
      name: 'mobile_device',
      components: {
        header: undefined,
        main: () => import('@/views/DeviceConsole.m.vue'),
        footer: undefined
      }
    },
    {
      path: '/device',
      name: 'device',
      components: {
        header: Header,
        main: () => import('@/views/DeviceConsole.vue'),
        footer: undefined
      },
      children: [
        {
          path: ':pk/:dn',
          name: 'console',
          component: () => import('@/components/Console.vue'),
          beforeEnter: (to, from, next) => {
            var isMobile = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i) ? true : false

            if (isMobile)
              next({ name: 'mobile_device', params: to.params });
            else
              next();
          },
        }
      ]
    },
    {
      path: '/application/fireAlarm',
      name: 'app_fa',
      components: {
        header: Header,
        main: () => import('@/views/FireAlarmApplication.vue'),
        footer: undefined
      },
      children: [
        {
          path: 'monitor',
          name: 'fa_monitor',
          component: () => import('@/components/FireAlarmApp/Monitor.vue')
        },
        {
          path: 'log',
          name: 'fa_log',
          component: () => import('@/components/FireAlarmApp/Log.vue')
        },
        {
          path: 'chart',
          name: 'fa_chart',
          component: () => import('@/components/FireAlarmApp/Chart.vue')
        }
      ]
    },
    // {
    //   path: '/device/:dn',
    //   name: 'device',
    //   components: {
    //     header: Header,
    //     main: () => import('@/views/DeviceConsole.vue'),
    //     footer: undefined
    //   }
    // },
    {
      path: '/application',
      name: 'application',
      components: {
        header: Header,
        main: () => import('@/views/Application.vue'),
        footer: undefined
      }
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      components: {
        header: Header,
        main: () => import('@/views/About.vue'),
        footer: undefined
      }
    }
  ]
})
